import React from 'react'
import {Button, Icon, Modal} from 'antd'
import {Link} from 'react-router-dom'
import './index.less'


export default class Frap extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            visiblePostLinks: false,
        }
    }

    showPostLinks = () => {
        this.setState({
            visiblePostLinks: true,
        });
    };
    handleOk = e => {
        console.log(e);
        this.setState({
            visiblePostLinks: false,
        });
    };

    handleCancel = e => {
        console.log(e);
        this.setState({
            visiblePostLinks: false,
        });
    };

    render() {
        return (
            <div>
                {/* 发布任务 */}
                <div className='frap' onClick={this.showPostLinks}>
                    <Button>
                        {/*<Icon type="plus-circle" style={{fontSize: '30px'}}/>*/}
                        <Icon type="plus" />
                        发布任务
                    </Button>
                </div>
                <Modal
                    visible={this.state.visiblePostLinks}
                    onOk={this.handleOk}
                    onCancel={this.handleCancel}
                    style={{textAlign: 'center'}}
                >
                    <Link to='/admin/customer/upload/question/1'>
                        <Button type='primary'>代办事情</Button>
                    </Link>
                    <br/>
                    <br/>
                    <Link to='/admin/customer/upload/question/2'>
                        <Button type='primary'>转发集赞</Button>
                    </Link>
                    <br/>
                    <br/>
                    <Link to='/admin/customer/upload/question/3'>
                        <Button type='primary'>填写问卷</Button>
                    </Link>
                    <br/>
                    <br/>
                    <Link to='/admin/customer/upload/question/0'>
                        <Button type='primary'>其他任务</Button>
                    </Link>
                </Modal>
            </div>
        )
    }
}